import React, { useState } from 'react';
import people from './data';
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from 'react-icons/fa';

const Review = () => {
  const [index, setIndex] = useState(0);
  const{name, job, image, text} = people[index];

  const checkNumber = (number) => {
    if(number > people.length - 1 ){
      return 0;
    }
    if(number < 0) {
      return people.length - 1;
    }
    return number ;
  };

  const nextPerson = () => {
    setIndex((index) => {
      let newIndex = index + 1;
      return checkNumber(newIndex);
    });
  };

  const prevPerson = () => {
    setIndex((index) => {
      let newIndex = index -1;
      return checkNumber(newIndex);
    });
  };
  
  /*burdaki olay şu random personı random numarayla seç ama o numara tam sayı olsun floor olsun, bunu insanların
  sayısı kadr yap, ve random number indexe eşit olsun eğer sayılar çakışırsa 1 ekle ve sürekli
  değilmiş olsun numaralar */
const randomPerson = () => {
  let randomNumber = Math.floor(Math.random() * people.length);
  if(randomNumber === index){
    randomNumber = index + 1
  }
  setIndex(checkNumber(randomNumber));

}

  return (
  <article className='review'>
    <div className='img-container'>
      <img src={image} alt={name} className='person-img' />
      <span className='quote-icon'>
        <FaQuoteRight />

      </span>

    </div>
    <h4 className='author'>{name}</h4>
    <p className='job'>{job}</p>
    <p className='info'>{text}</p>
    <div className='button-container'></div>
    <button className='prev-btn' onClick={prevPerson}>
      <FaChevronLeft />
    </button>

    <button className='next-btn' onClick={nextPerson}>
      <FaChevronRight />
    </button>

    <button className='random-btn'onClick={randomPerson}
      >Süprüzlü yorum bul!
      
    </button>
  </article>
  );
};

export default Review;
